/* ******TEAM BANNER*********/

.team__team-banner {
    width: 100%;
    height: 85%;
}

.team__team-banner .row {
    height: 33%;
    width: 100%;
    margin: 0;
}

.team__team-banner .col {
    position: relative;
    min-width: 20%;
    padding: 0%;
    max-height: 100%;
}

.team__team-banner .col .holder {
    position: absolute;
    width: 100%;
    height: 100%;
    filter: saturate(0);
    top: 0%;
    left: 0%;
    object-fit: cover;
}

.team__team-banner .col .holder-2 {
    position: absolute;
    width: 100%;
    height: 100%;
    filter: saturate(0);
    z-index: -10000;
    object-fit: cover;
    top: 0%;
    left: 0%;
}

.animated {
    animation-name: fade;
    animation-duration: 3s;
    animation-delay: 0ms;
    animation-timing-function: linear;
}

@keyframes fade {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.team__team-banner .col-2 {
    min-width: 40%;
    background-color: white;
    padding: 1% 1%;
    max-height: 100%;
}

.team__banner-heading {
    font-family: "Poppins Bold";
    margin-top: 1%;
    font-size: 24px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.76;
    letter-spacing: normal;
    color: #4d8c52;
}

.team__banner-text {
    font-family: "Poppins Regular";
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.75;
    letter-spacing: normal;
    color: rgba(33, 56, 66, 0.6);
}

@media screen and (max-width: 768px) {
    .team__banner-heading {
        font-size: 16px;
        line-height: normal;
    }
    .team__banner-text {
        font-size: 14px;
        line-height: normal;
    }
}

@media screen and (max-width: 576px) {
    .team__team-banner {
        height: 90%;
        overflow-x: hidden;
    }
    .team__team-banner .col {
        min-width: 33.33%;
    }
    .team__team-banner .col-2 {
        min-width: 33.33%
    }
    .col-m {
        display: none;
    }
    .team__banner-heading {
        font-size: 12px;
        margin-bottom: 0%;
        line-height: normal;
    }
    .team__banner-text {
        font-size: 10px;
        line-height: normal;
    }
    .holder,
    .holder-2 {
        object-fit: fill !important;
    }
}

@media screen and (max-width: 320px) {
    .team__banner-heading {
        font-size: 10px;
    }
    .team__banner-text {
        font-size: 8px;
    }
}


/* *********SECOND SECTION********** */

.team__second-section {
    margin-top: 3%;
    padding: 3% 11.8% !important;
    margin-bottom: 5%;
    width: 82.56%;
}

.team__second-section-heading {
    font-family: "Poppins ExtraBold";
    font-size: 24px;
    font-weight: 800;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.34;
    letter-spacing: normal;
    color: #4d8c52;

}

.team__second-section-text {
    font-family: "Poppins Regular";
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #818383;
}

@media screen and (max-width: 768px) {
    .team__second-section {
        margin-top: 7%;
        width: 90%;
        text-align: center;
    }
    .team__second-section-heading {
        font-size: 18px;
    }
    .team__second-section-text {
        font-size: 14px;
    }
}

.founders {
    padding: 5% 11% 5% 11.86% !important;
    background: linear-gradient(to bottom, #e7f6e8, #ffffff);
}

.team__third-section .founder-card {
    width: 5%;
    padding: 0% 8% 0% 0%;
    margin-bottom: -10%;
    transform: translateY(-30%);
}

.founder-image {
    width: 100%;
    margin-bottom: 5%;
}

.founder-name {
    font-family: "Poppins Bold";
    font-size: 24px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    margin-bottom: 1%;
    color: #000000;
}

.founder-desi {
    font-family: "Poppins Regular";
    font-size: 18px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #818383;
}

.founder-text {
    opacity: 0.7;
    font-family: "Poppins Regular";
    font-size: 16px;
    width: 100%;
    text-align: justify;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
}

.founder-logos {
    display: inline-block;
    width: 100%;
}

.founder-logos i {
    color: #737373;
    padding: 2%;
}

@media screen and (max-width: 768px) {
    .team__third-section .founder-card {
        width: 100%;
        margin-bottom: 25%;
        transform: none;
    }
    .founder-name {
        font-size: 18px;
    }
    .founder-desi {
        font-size: 16px;
    }
    .founder-text {
        font-size: 14px;
    }
}